/*!
* --------------------------------------------------
* Modals
* --------------------------------------------------
*/

.modal-header,
.modal-body,
.modal-footer {
  color: #000000;
}

.modal-header {
  padding: 15px;
  background: $modal-header-bg;
  border-bottom: none !important;
  color: $modal-header-color;
  box-shadow: 0px 2px 2px 0px rgba(50, 50, 50, 0.24);
  min-height: 16.42857143px;
}

.modal-content {
  background-color: #ffffff;
  border: 0px transparent;
  border-radius: 0px;
}

.close {
  &,
  &:hover,
  &:focus,
  &:active {
    opacity: 0.7;
    color: #fff;
  }
}
